* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  background: #333;
}

.container {
  width:75%;
	margin:auto;
	overflow: hidden;
}

a {
  text-decoration: none;
}

/*
  Media querries: 
    0-400px - small phone
    400px-600px - phone
    600-900px - tablet portrait mode
    900-1200px - tablet landscape
    1200-1800px - desktop
    1800+px - big desktop 
*/